import { Component, EventEmitter, Input, Output, OnInit, ViewEncapsulation } from '@angular/core';

import { EntrustService } from 'src/app/entrust.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { isTemplateHead } from 'typescript';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-details-inventory',
  templateUrl: './details-inventory.component.html',
  styleUrls: ['./details-inventory.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class DetailsInventoryComponent implements OnInit {


  @Output() countChanged=   new EventEmitter<string>();

  //@Input() public inventoryDetails: FormGroup;
  heroes = [{ id: 1, name: 'Standard' },{ id: 2, name: 'Advantage' },{ id: 3, name: 'EV-Multi Domain' },
  { id: 4, name: 'UC-Multi Domain' },
  { id: 5, name: 'Wild Card' },
];
inventories: any=[];


  selectedHero:Hero;
  selectedname:any;
  selectedIndex = 1;
  access_token: any;

  constructor(private router: Router,public entrustservice:EntrustService,private httpClient:HttpClient) { }

  ngOnInit() {
    this.entrustservice.getInventry().subscribe((response : any)=>
    {
      const obj =JSON.parse(response);
      this.inventories=obj.inventories;
      })

  }

  onSelect(hero: Hero): void {
    this.selectedHero = hero;
    this.selectedIndex = this.selectedHero.id;
    this.selectedname = this.selectedHero.name;
    this.countChanged.emit(this.selectedname);

//this.entrustservice.changeMessage( this.selectedname);

  }



}
export interface Hero {
  id: number;
  name: string;
}


