  public selectionChangeseca(v: any): void {
    

    this.optionseca = v.value;
    if (v.value == 1) {
      this.csrazureprop = {
        crv: "P-256",
        keyType: v.name,
        keysize: 256,
        kid: this.uid
      }
    } else {

      this.csrazureprop = {
        crv: "",
        keyType: v.name,
        keysize: 2048,
        kid: this.uid
      }

    }
   
  }
