import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { EntrustService } from 'src/app/entrust.service';
import { environment } from 'src/environments/environment';


@Component({
  selector: 'app-managed-certificate',
  templateUrl: './managed-certificate.component.html',
  styleUrls: ['./managed-certificate.component.css']
})
export class ManagedCertificateComponent implements OnInit {
  //baseUrl =environment.apiserver;
  RevocationReasons:any=['Superseded', 'Cessation Of Operation','Affiliation Changed', 'Key Compromised'];
  Revocationfinal:any=[{text: 'Superseded',value:'superseded'}, {text: 'Cessation Of Operation',value:'cessationOfOperation'
  }, {text: 'Affiliation Changed',value:'affiliationChanged'}, {text: 'Key Compromised',value:'keyCompromise'
   }];

  optionValue:any;
  public gridData=[];
  public gridView=[];
  hide:any;
  type:any;
  revokevalue:any;
  revokeCmnt:any;
  selectedValue:any;
  public mySelection: string[] = [];
  id:any;
 
  loading: boolean = true;
  public opened = false;
  public errorOpen = false;
  errormessage: any;

  public close(status) {

    this.opened = false;
    this.errorOpen = false;
  }
 
  isDisabled:boolean=true;
  public open() {
    this.opened = true;
  }
  public model = {
    gender: null
};
//loading: boolean= true;
  constructor(private router: Router,private esc:EntrustService,private httpclient:HttpClient,private route: ActivatedRoute) {
  // this.id = this.route.snapshot.paramMap.get("id");
  this.id = localStorage.getItem('checkOutData');
  }
   ngOnInit() {

  
    this.getbyid();
   }

   getbyid(){
    this.esc.getBytrackingid(this.id).subscribe((data:any)=>
    
    {
      //QA

      const response =JSON.parse(data);
      this.revokevalue=response;
      this.loading=false;

      this.gridView.push(response);

      //QA
     // this.revokevalue=data;
     //this.gridView.push(data);

     })
   }
   closed(){
    this.opened = false;
    this.router.navigate(['/']);
   }
   selectionChangeskey(){
    
     this.isDisabled=false;
   }
   public submit(): void {
  
    this.loading=true;
    var reason=this.Revocationfinal.filter(r=>r.text===this.selectedValue);

    var csrObj =
      {
        crlReason: reason[0].value,
        revocationComment:this.revokeCmnt
      }
      //console.log(csrObj)
  //this.loading = true;
 



this.esc.crtrevoke(csrObj, this.id).subscribe(
  res => {
    this.loading = false;
    if(res ==''){
      this.opened=true;
return false;
    }
    const obj =JSON.parse(res);
    //console.log(obj);
    if(obj.trackingId){
      this.opened=true;
      return false;
      }
    switch(obj.status){
      case  400:


        this.errormessage=obj.errors[0].message;
        this.errorOpen =true;
        return false;

      case 200:

        this.opened=true;
        return false;
      case 500:
        this.loading = false;

        this.errormessage="Azure Internal Sever Error";
        this.errorOpen =true;
        return false;

           case(obj.trackingId):
      this.loading = false;
      this.opened=true;
    
      return false;
      case 401:
        this.loading = false;

        this.errormessage=obj.errors[0].message;
        this.errorOpen =true;
        return false;

           case(obj.trackingId):
      this.loading = false;
      this.opened=true;
    
      return false;

      }


  },err => {  this.loading = false;

    switch(err.status){
      case  0:
      //  alert("Entrust Internal server error")
        this.errormessage="Azure Internal server error";
        this.errorOpen =true;

        return false;

      case  500:
      //  alert("Entrust Internal server error")
        this.errormessage="Azure Internal Server error";
        this.errorOpen =true;
        return false;


    }
  }
    )

  }
}
