import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http'

import { BehaviorSubject, Observable, Subject, throwError } from 'rxjs';
import { catchError, map, publishReplay, refCount } from 'rxjs/operators'
import { environment } from 'src/environments/environment';


export interface myData {
  id: string;
}
@Injectable({
  providedIn: 'root'
})
export class EntrustService {
 //comment this line while build the project
  
//baseUrl = environment.apiserver;

  //uncomment this line while build the project
 baseUrl=localStorage.getItem('baseUrl');

 
 // id = localStorage.getItem('checkOutData');
  uid = localStorage.getItem("tempid");


   data:any[];
  invenotryData: any[];
  orgData: any;

    setData(data:any[]){        this.data = data    } 
    setInventoryData(data:any[]){        this.invenotryData = data    } 
    setorgData(data:any[]){        this.orgData = data    } 

    getData(){        return this.data || [];    }
    getInventoryData(){        return this.invenotryData || [];    }
    getorgData(){        return this.orgData || [];    }

    hasData(){
       // return this.data && this.data['certificates'].length;    
        
       if (this.data)
       {         return true;       }
        
    }  
    hasInventoryData(){
      if (this.invenotryData){
        return true;
      }
    }
    hasorgData(){
      if (this.orgData){
        return true;
      }
    }
  constructor(private httpClient: HttpClient) {  }





  azurecrt(name, csrObj) {
    return this.httpClient.post(this.baseUrl+'api/crmake/'+this.uid + '/'+name, csrObj).pipe(
      map(resp => resp),
      catchError((err) => { return throwError(err); }))
  }
  azureMergeCrt(crtname,mrgobj){
    return this.httpClient.post<any>(this.baseUrl+'api/mergeCertificate/'+this.uid+'/'+crtname,mrgobj).pipe(map(resp=>resp),
    catchError((err)=>{return throwError(err);}))
  }
  usercrt(csrObj) {
    return this.httpClient.post<any>(this.baseUrl+'api/ecrcertificate/'+this.uid, csrObj).pipe(
      map(resp => resp),
      catchError((err) => { return throwError(err); }))
  }

  reverifyDomain(csrObj, domain, clientId) {
    
    return this.httpClient.put<any>(this.baseUrl+'api/domain/' + clientId + '/' + domain, csrObj).pipe(
      map(resp => resp),
      catchError((err) => { return throwError(err); }))
  }

  // reverifyDomain(csrObj,clientId,domain) {
  //   return this.httpClient.post<any>(this.authBaseUrl + "/client/validateOtp/" + clientId + "/" + domain, csrObj) .pipe(
  //     map(resp => resp),
  //     catchError(this.errorHandler)
  // )
  // }
  crtrenew(csrObj, id) {
    return this.httpClient.post<any>(this.baseUrl+ 'api/renew/' + id+'/'+this.uid, csrObj).pipe(
      map(resp => resp),
      catchError(err => {
        throw err;
      })
    )
  }

  crtrissue(csrObj, id) {
    return this.httpClient.post<any>(this.baseUrl+ 'api/reissue/' + id +'/'+this.uid, csrObj).pipe(
      map(resp => resp),
      catchError(err => {
        throw err;
      })
    )
  }
  crtrevoke(csrObj, id) {
    return this.httpClient.post<any>(this.baseUrl+ 'api/revoke/' + id+'/'+this.uid, csrObj).pipe(
      map(resp => resp),
      catchError(err => {
        throw err;
      })
    )
  }

  // getAll() { return this.httpClient.get<any>(this.baseUrl + 'api/clients/') }


  //getByStatus(){return this.httpClient.get<any>('https://entrustapitest.azurewebsites.net/api/secrets').pipe() }
  //getByStatus(){return this.httpClient.get<any>(this.baseUrl +'certificates?sort=trackingId:desc&limit=1000&fields=*&status=in:ACTIVE,READY').pipe() }
  
  //getByStatus() { return this.httpClient.get<any>(this.baseUrl + 'api/Certificates/'+this.uid).pipe(publishReplay(),refCount())};
  //getByStatus() { return this.httpClient.get<any>(this.baseUrl + 'api/test/').pipe(publishReplay(),refCount())};
 
  getByStatus() { return this.httpClient.get<any>(this.baseUrl + 'api/Certificates/'+this.uid).pipe(catchError((err) => { return throwError(err); })) }
  getBytrackingid(id) { return this.httpClient.get<any>(this.baseUrl + 'api/certificates/' + id +'/'+this.uid).pipe(catchError((err) => { return throwError(err); })) }
  getOrganization() { return this.httpClient.get<any>(this.baseUrl + 'api/organizations/'+this.uid).pipe(catchError((err) => { return throwError(err); })) }
  getOrganizationUnit() { return this.httpClient.get<any>(this.baseUrl + 'api/organizationalUnits/'+this.uid).pipe(catchError((err) => { return throwError(err); })) }
  getInventry() { return this.httpClient.get<any>(this.baseUrl + 'api/inventories/'+this.uid).pipe(catchError((err) => { return throwError(err); })) }
  getcustomFields() { return this.httpClient.get<any>(this.baseUrl + 'api/customFields/'+this.uid).pipe(catchError((err) => { return throwError(err); })) }

  getAuditlogs() { return this.httpClient.get<any>(this.baseUrl + 'api/auditlogs/'+this.uid).pipe(catchError((err) => { return throwError(err); })) }
  getdomain() { return this.httpClient.get<any>(this.baseUrl + 'api/domains/'+this.uid).pipe(catchError((err) => { return throwError(err); })) }
  getClients() { return this.httpClient.get<any>(this.baseUrl + 'api/clients/'+this.uid).pipe(catchError((err) => { return throwError(err); })) }
  getprofile() { return this.httpClient.get<any>(this.baseUrl + 'api/profile').pipe(catchError((err) => { return throwError(err); })) }



}
