  //hold for now
  resubmitDomain() {

    var domainName = this.domainVerify.dataItem.domainName;
    var id = this.domainVerify.dataItem.clientId;
    var domainObj =
    {
      "verificationMethod": "DNS",

    }
    this.esc.reverifyDomain(domainObj, domainName, id).subscribe(

      res => {
        const obj = JSON.parse(res);
        //console.log(obj);
        if (obj.trackingId) {
          this.loading = false;
          this.router.navigate(['/success']);
        }
        switch (obj.status) {
          case 400:
            this.loading = false;
            alert("Something  Wrong, " + obj.errors[0].message);
            return false;
            break;
          case 200:
            this.loading = false;
            this.router.navigate(['/success']);
            break;
          case 500:
            this.loading = false;
            alert("Azure Internal Sever Error");
            return false;
            break;
          case (obj.trackingId):
            this.loading = false;
            this.router.navigate(['/success']);

        }

      })
  }
