import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'customPipe'
})
export class CustomPipePipe implements PipeTransform {

  public transform(input:string): string{
    //console.log(input);
    if (!input) {
        return '';
    } else {
        return input.replace(/[\W_]/g, ' ') ;


    }
}

}
