import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DetailsComponent } from './Cleints/details/details.component';
import { DetailsCertificateComponent } from './Certificate/Detailscertificate';
import { DetailsOrganizationComponent } from './organization/details-organization/details-organization.component';
import { DetailsInventoryComponent } from './Inventry/details-inventory/details-inventory.component';
import { DetailsAuditlogsComponent } from './AuditLogs/details-auditlogs/details-auditlogs.component';
import { DetailsDomainComponent } from './Domain/details-domain/details-domain.component';
import { LoginComponent } from './pages/login/login.component';
import { AccountConfigurationComponent } from './pages/account-configuration/account-configuration.component';

import { SslTlsComponent } from './Create/ssl-tls/ssl-tls.component';
import { ManagedCertificateComponent } from './Certificate/managed-certificate/managed-certificate.component';
import { ActiveCertificatesComponent } from './Certificate/active-certificates/active-certificates.component';
import { RenewProductnameComponent } from './Certificate/renew-productname/renew-productname.component';
import { ReissueCertificateComponent } from './Certificate/reissue-certificate/reissue-certificate.component';
import { DashBoardComponent } from './pages/dash-board/dash-board.component';
import { SuccessComponent } from './pages/success/success.component';
import {AuthNavbarGuard} from './auth-navbar.guard';
import { HelpguideComponent } from './pages/helpguide/helpguide.component';




const routes: Routes = [
  //{ path: '', redirectTo: '', pathMatch: 'full'},
  { path: '', component: ActiveCertificatesComponent },
  { path: 'success', component: SuccessComponent },
  { path: 'domain', component: DetailsDomainComponent },


{ path: 'ssl-tls', component: SslTlsComponent },
{ path: 'certificates/:id', component: DetailsCertificateComponent },
{ path: 'renew', component: RenewProductnameComponent , canActivate : [AuthNavbarGuard] },
{ path: 'reissue', component: ReissueCertificateComponent , canActivate : [AuthNavbarGuard] },
{ path: 'revoke', component: ManagedCertificateComponent , canActivate : [AuthNavbarGuard] },
{ path: 'accountConfigure', component: AccountConfigurationComponent },
{ path: 'help', component: HelpguideComponent },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
